.icon {
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 10px;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.icon img {
  width: 30px;
  height: 30px;
}

.icon.left {
  left: 0;
}

.icon.right {
  right: 0;
}

.icon:hover {
  cursor: pointer;
}

.icon.pointRight {
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
}



