.c-searchBox {
  position: relative;
  display: block;
  width: 370px;
  height: 50px;
  text-align: left;
}

.c-searchBox input {
  width: 100%;
  color: #333;
  line-height: 30px;
  font-family: sans-serif;
  font-size: 14px;
  padding: 10px 40px 10px 10px;
  border: none;
  font-weight: lighter;
}

.c-searchBox:hover input,
.c-searchBox input:focus {
  -webkit-box-shadow: 0 3px 8px 0 rgba(0,0,0,0.2), 0 0 0 1px rgba(0,0,0,0.08);
          box-shadow: 0 3px 8px 0 rgba(0,0,0,0.2), 0 0 0 1px rgba(0,0,0,0.08);
  outline: 0;
  border: none;
}



